.btn-primary,
.btn-primary:active,
.btn-primary:visited,
.btn-primary:focus {
  background-color: #e94560;
  border-color: #e94560;
}

.btn-primary:hover {
  background-color: #c0374e;
  border-color: #c02e47;
}

.hover-image {
  margin-top: 0px;
  animation: MoveUpDown 1.5s linear infinite;
}

.hover-image-delay {
  margin-top: 10px;
  animation: MoveDownUp 1.5s linear infinite;
}

@keyframes MoveDownUp {
  50% {
    margin-top: 0px;
  }
}

@keyframes MoveUpDown {
  50% {
    margin-top: 10px;
  }
}

.invalid-msg {
  animation: fadein 1s;
}

@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.nav-link {
  color: white;
  font-weight: 400;
}

.nav-link:hover {
  color: #e94560;
}

.nav-link-active {
  background-color: #e94560;
}

.nav-link-active:hover {
  color: white !important;
}

.stock-link {
  color: #e94560;
}

.stock-link:hover {
  color: #d83853;
}

.spinner-border {
  color: #e94560 !important;
}

.blinking {
  animation: blink-animation 1s step-start 0s infinite;
  -webkit-animation: blink-animation 1s step-start 0s infinite;
}

@keyframes blink-animation {
  50% {
    opacity: 0;
  }
}
@-webkit-keyframes blink-animation {
  50% {
    opacity: 0;
  }
}

.grow {
  transition: transform 0.2s;
}

.grow:hover {
  transform: scale(1.25);
}
